<template>
  <div class="main-banner__container">
    <div class="background__container">
      <div class="main-content__wrap">
        <div class="main-content__objet">
          <transition name="fade">
            <img  v-if="logoFlag" src="../../assets/main/prdemo_outline.svg" alt="prography demo day">
          </transition>
        </div>
        <transition name="fade">
          <div  v-if="textFlag" class="main-content__text">
            세상에 필요한 서비스를 만들어내는 프로그라피에서<br/>
            5기 활동의 결과물을 공개합니다!<br/>
            프로그라피 5기에는 총 10개의 멋진 팀이 참여하였으며<br/>
            클로즈 베타와 오픈 베타를 거쳐 배포를 완료하였습니다.<br/>
            2020년 2월 8일 토요일, 역삼동 마루 180에서<br/>
            프로그라피의 멋진 프로젝트들을 만나보세요!
          </div>
        </transition>
        <div class="main-content__button-wrapper">
          <transition name="fade-op">
            <button  v-if="buttonFlag" type="button" class="main-content__button"  @click="scrollToInvitation()">
              행사가 취소되었습니다 &nbsp; :<
            </button>
          </transition>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    name: "mainBlock",
    data() {
      let logoFlag = false;
      let textFlag = false;
      let buttonFlag = false;
      return {
        logoFlag,
        textFlag,
        buttonFlag
      }
    },
    created(){

    },
    mounted() {
      setTimeout(this.turnFlag, 200, 0);
      setTimeout(this.turnFlag, 500, 1);
      setTimeout(this.turnFlag, 1000, 2);

    },
    methods: {
      scrollToInvitation() {
        let invitationDOM = document.getElementById("invitation");
        invitationDOM.scrollIntoView();
      },
      turnFlag(step){
        switch (step) {
          case 0:
            this.logoFlag = true;
            break;
          case 1:
            this.textFlag = true;
            break;
          case 2:
            this.buttonFlag = true;
            break;
        }
      }
    }
  }
</script>

